<template>
  <div ref="content">
    <div class="d-none d-md-none d-lg-flex">
      <b-button
        @click="unReadList()"
        :class="{ mntbtn: activeClassFlg, mnt2btn: notActiveFlg }"
        class="mntborder"
        >{{ $t("UNREADLIST") }}
      </b-button>
      <b-button
        @click="ReadList()"
        :class="{ mntbtn: notActiveFlg, mnt2btn: activeClassFlg }"
        class="border-radius-imprv"
        >{{ $t("READLIST") }}
      </b-button>
    </div>
    <br />
    <div
      class="d-md-flex d-lg-none d-flex align-content-item justify-content-center"
    >
      <b-button
        @click="unReadList()"
        :class="{ mntbtn: activeClassFlg, mnt2btn: notActiveFlg }"
        class="mntborder"
        >{{ $t("UNREADLIST") }}
      </b-button>
      <b-button
        @click="ReadList()"
        :class="{ mntbtn: notActiveFlg, mnt2btn: activeClassFlg }"
        class="border-radius-imprv"
        >{{ $t("READLIST") }}
      </b-button>
    </div>
    <br />
    <template v-for="message in messages" class="">
      <div :key="message.id" class="card-margin">
        <b-row
          class="d-none d-md-none d-lg-flex dropdownToggleBody mx-auto mb-3"
        >
          <!-- v-if="message.messageType == '002'" for supplier reply screen messages -->
          <b-row>
            <span class="thumbnailLogo ml-4 mt-2">
              <img
                v-if="message.messageType == '002'"
                src="@/assets/ReductionProposal.png"
                width="100%"
              />
              <img
                v-else-if="message.messageType == '006'"
                src="@/assets/Subsidy.png"
                width="100%"
              />
              <img v-else src="@/assets/Notification.png" width="100%" />
            </span>
          </b-row>
          <div class="pd-23">
            <b-row
              :class="message.message1.length > 15 ? 'mobileMargin' : ''"
              class="ml-1 mb-1 mt-2"
            >
              {{ message.time }}
            </b-row>
            <b-row
              :class="
                message.message1.length > 15 ? 'padingAllign messageClass' : ''
              "
              class="mb-2 pl-1 messageClass"
            >
              <b-col
                v-if="
                  message.messageType == '002' || message.messageType == '006'
                "
              >
                <span v-html="sliceText(message.message1)"></span>
              </b-col>
              <b-col v-else>
                <span
                  v-if="expendedId && expendedId == message.id"
                  v-html="message.message1"
                >
                </span>
                <span v-else v-html="sliceText(message.message1)"></span>
              </b-col>
            </b-row>
          </div>
          <b-col
            class="float-right align-items-center d-flex justify-content-end"
          >
            <b-link
              v-if="!(expendedId && expendedId == message.id)"
              @click="
                readMore(
                  message.id,
                  message.messageType,
                  message.id,
                  message.reductionProposalNumber
                )
              "
              href="javascript:;"
              class="card-link float-right link"
            >
              <img src="@/assets/arrow-icon02.svg" width="50%" />
            </b-link>
            <b-link
              v-else
              @click="readLess()"
              href="javascript:;"
              class="card-link float-right link"
            >
              <img src="@/assets/arrow-icon02.svg" width="50%" />
            </b-link>
          </b-col>
        </b-row>
        <b-row class="d-lg-none d-md-flex dropdownToggleBody mb-3 mx-auto">
          <b-col
            cols="1"
            class="align-items-center d-flex justify-content-start ml-3"
          >
            <div class="thumbnailLogo">
              <img
                v-if="message.messageType == '002'"
                src="@/assets/ReductionProposal.png"
                width="100%"
              />
              <img
                v-else-if="message.messageType == '006'"
                src="@/assets/Subsidy.png"
                width="100%"
              />
              <img v-else src="@/assets/Notification.png" width="100%" />
            </div>
          </b-col>
          <b-col cols="10" class="mobileAdjust">
            <div
              v-if="!(expendedId && expendedId == message.id)"
              @click="
                readMore(
                  message.id,
                  message.messageType,
                  message.id,
                  message.reductionProposalNumber
                )
              "
              class="pd-23"
            >
              <b-row class="ml-1 mb-1 mt-3">
                {{ message.time }}
              </b-row>
              <b-row class="mb-3 pl-1 messageClass">
                <b-col
                  v-if="
                    message.messageType == '002' || message.messageType == '006'
                  "
                  class="pr-0"
                >
                  <span v-html="sliceText(message.message1)"></span>
                </b-col>
                <b-col v-else>
                  <span
                    v-if="expendedId && expendedId == message.id"
                    v-html="message.message1"
                  >
                  </span>
                  <span v-else v-html="sliceText(message.message1)"></span>
                </b-col>
              </b-row>
            </div>
            <div v-else @click="readLess()" class="pd-23">
              <b-row class="ml-1 mb-1 mt-3">
                {{ message.time }}
              </b-row>
              <b-row class="mb-3 pl-1 messageClass">
                <b-col
                  v-if="
                    message.messageType == '002' || message.messageType == '006'
                  "
                >
                  <span v-html="sliceText(message.message1)"></span>
                </b-col>
                <b-col v-else>
                  <span
                    v-if="expendedId && expendedId == message.id"
                    v-html="message.message1"
                  >
                  </span>
                  <span v-else v-html="sliceText(message.message1)"></span>
                </b-col>
              </b-row>
            </div>
          </b-col>
          <b-col
            cols="1"
            class="float-right align-items-center d-none d-lg-flex d-md-none justify-content-end"
          >
            <b-link
              v-if="!(expendedId && expendedId == message.id)"
              @click="
                readMore(
                  message.id,
                  message.messageType,
                  message.id,
                  message.reductionProposalNumber
                )
              "
              href="javascript:;"
              class="card-link float-right link"
            >
              <!-- <u>{{ $t("READMORE") }}</u> -->
              <img src="@/assets/arrow-icon02.svg" />
            </b-link>
            <b-link
              v-else
              @click="readLess()"
              href="javascript:;"
              class="card-link float-right link"
            >
              <img src="@/assets/arrow-icon02.svg" />
            </b-link>
          </b-col>
        </b-row>
      </div>
    </template>
    <div class="d-flex justify-content-center">
      <div class="row mt-3">
        <div v-if="messages.length > 0" class="col-md-12">
          <nav>
            <ul class="pagination mr-2">
              <li
                v-bind:class="{ disabled: !pagination.prev_link }"
                class="page-item d-flex justify-content-center"
              >
                <span
                  @click="
                    getNotificationMessagesList(
                      pagination.prev_link,
                      'link_type_string'
                    )
                  "
                  role="menuitem"
                  aria-label="Go to previous page"
                  aria-disabled="true"
                  class="page-link textClass"
                >
                  <i
                    data-v-171636d7=""
                    aria-hidden="true"
                    class="fa fa-caret-left text-muted mr-2"
                  >
                  </i>
                  前へ</span
                >
              </li>
              <template v-for="n in pagination.last_page">
                <li
                  v-if="n == 1"
                  v-bind:key="n"
                  v-bind:class="{ active: pagination.current_page == n }"
                  class="page-item ml-3"
                >
                  <a
                    @click="getNotificationMessagesList(n)"
                    href="#"
                    class="page-link"
                    >{{ n }}</a
                  >
                </li>
                <li
                  v-else-if="
                    n < pagination.current_page + 2 &&
                    n > pagination.current_page - 2
                  "
                  v-bind:key="n"
                  v-bind:class="{ active: pagination.current_page == n }"
                  class="page-item ml-2"
                >
                  <a
                    @click="getNotificationMessagesList(n)"
                    href="#"
                    class="page-link"
                    >{{ n }}</a
                  >
                </li>
                <li v-else-if="n == 2" v-bind:key="n" class="page-item ml-2">
                  <a href="javascript:void(0);" class="page-link">...</a>
                </li>
                <li
                  v-else-if="n == pagination.last_page - 1"
                  v-bind:key="n"
                  class="page-item ml-2"
                >
                  <a href="javascript:void(0);" class="page-link">...</a>
                </li>
                <li
                  v-else-if="n == pagination.last_page"
                  v-bind:key="n"
                  class="page-item ml-2"
                >
                  <a
                    @click="getNotificationMessagesList(n)"
                    href="#"
                    class="page-link"
                    >{{ n }}</a
                  >
                </li>
              </template>
              <li
                v-bind:class="{ disabled: !pagination.next_link }"
                class="page-item ml-2"
              >
                <span
                  @click="
                    getNotificationMessagesList(
                      pagination.next_link,
                      'link_type_string'
                    )
                  "
                  role="menuitem"
                  aria-label="Go to previous page"
                  aria-disabled="true"
                  class="page-link textClass"
                >
                  次へ<i
                    data-v-171636d7=""
                    aria-hidden="true"
                    class="fa fa-caret-right text-muted ml-2"
                  >
                  </i
                ></span>
              </li>
            </ul>
          </nav>
        </div>
        <div v-else class="col-md-12">
          {{ $t("NOUNREADMESSAGES") }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { Config } from "@/shared/config";
import api from "@/shared/api";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "ReductionProposal",
  components: {},
  data() {
    return {
      activeClassFlg: true,
      notActiveFlg: false,
      perPage: 3,
      currentPage: 1,
      expendedId: "",
      disabled: false,
      reductionProposalNumber: "",
      messages: [],
      onlyRead: false,
      allMessages: [
        "00",
      ] /** 00 -----> Unread messages  01-----> read messages */,
      pagination: {},
      loading: false,
    };
  },
  computed: {
    rows() {
      return this.messages.length;
    },
    lists() {
      const items = this.messages;
      // Return just page of items needed
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
    isMobileScreen() {
      if (
        navigator.userAgent.match(/Android/i) ||
        navigator.userAgent.match(/webOS/i) ||
        navigator.userAgent.match(/iPhone/i) ||
        navigator.userAgent.match(/iPad/i) ||
        navigator.userAgent.match(/iPod/i) ||
        navigator.userAgent.match(/iPadpro/i) ||
        navigator.userAgent.match(/BlackBerry/i) ||
        navigator.userAgent.match(/Windows Phone/i)
      ) {
        return true;
      } else {
        return false;
      }
    },
    getScreenWidth() {
      return window.screen.width;
    },
  },
  async mounted() {
    await this.getNotificationMessagesList();
  },
  created() {},
  methods: {
    unReadList() {
      this.activeClassFlg = true;
      this.notActiveFlg = false;
      this.onlyRead = false;
      this.filterByRead();
    },
    ReadList() {
      this.activeClassFlg = false;
      this.notActiveFlg = true;
      this.onlyRead = true;
      this.filterByRead();
    },
    /**Get the list of notification messages from API and assign to variables */
    async getNotificationMessagesList(page, linkType) {
      let data;
      let url = `${Config.apiUrl}`;

      this.loading = true;
      this.disabled = true;
      if (linkType == "link_type_string") {
        let path = page.replace(url, "");
        this.session_id = localStorage.getItem("client_account_no");
        data = await api.post(path, {
          client_no: this.session_id,
          status_array: this.allMessages,
        });
      } else {
        this.session_id = localStorage.getItem("client_account_no");
        data = await api.post(`${endPoint.MESSAGELIST}?page=${page}`, {
          client_no: this.session_id,
          status_array: this.allMessages,
        });
      }
      this.loading = false;
      this.disabled = false;
      let mapData = [];
      this.pagination = {
        current_page: data.data.data.current_page,
        last_page: data.data.data.last_page,
        from_page: data.data.data.from,
        to_page: data.data.data.to,
        total_page: data.data.data.total,
        path_page: data.data.data.path + "?page=",
        first_link: data.data.data.first_page_url,
        last_link: data.data.data.last_page_url,
        prev_link: data.data.data.prev_page_url,
        next_link: data.data.data.next_page_url,
      };
      data.data.data.data.forEach((item) => {
        let messageData = {
          id: item.message_no,
          isSeen: item.message_sts_kbn,
          message1: item.message_text ? item.message_text : "",
          title: item.message_title ? item.message_title : "",
          time:
            item.message_dt.slice(0, 4) +
            "/" +
            item.message_dt.slice(6, 7) +
            "/" +
            item.message_dt.slice(8, 10),
          messageType: item.message_type,
          reductionProposalNumber:
            item.message_type == "002"
              ? item.reduction_proposal_no
              : item.assess_req_no,
        };
        mapData.push(messageData);
      });

      this.messages = mapData;
    },

    /**filter the messages list based on checkbox (clicked or not) to display only read messages by calling same API function call */
    filterByRead() {
      setTimeout(() => {
        if (this.onlyRead) {
          /** 00 -----> get only Unread messages */
          this.allMessages = ["00", "01"];
          this.getNotificationMessagesList();
        } else {
          /** 00 ----->Get both Unread messages  01-----> read messages */
          this.allMessages = ["00"];
          this.getNotificationMessagesList();
        }
      }, 0);
    },
    /**Function to load all Unread messages in listing screen */
    async getUnreadNotifications() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MESSAGEREDORUNREDCOUNT,
        {
          client_no: localStorage.getItem("client_account_no"),
        }
      );
      if (data.success) {
        this.$store.dispatch("updateUnreadMessages", {
          unread_messages: data.data.messageCount,
        });
      }
    },
    /**this function will allow you to expand the notification messages to read fully */
    /**In case its a proposal message it will pass the data to next screen and route */
    async readMore(message_no, messageType, expendedId, reduction_proposal_no) {
      const { data } = await BasicInfoApis.postAPI(endPoint.MESSAGEMARKED, {
        message_no: expendedId,
      });
      if (data.success) {
        console.log("MarkRead Updated to DB", this.messages);
        this.messages = this._.map(this.messages, (item) => {
          return item.id == expendedId
            ? Object.assign(item, { isSeen: true })
            : item;
        });
        this.getUnreadNotifications();
      }
      if (messageType == "002") {
        this.$store.dispatch("updateProposalInfo", {
          message_no: message_no,
          proposal_no: reduction_proposal_no ? reduction_proposal_no : "",
          message_type: messageType,
        });
        return this.$router.push({ name: "proposal_message" });
      }
      if (messageType == "006") {
        //While binding list date messageType 006 it will take assess_req_no only
        console.log("assess_req_no", reduction_proposal_no);
        this.$store.dispatch("updateProposalInfo", {
          message_no: message_no,
          proposal_no: reduction_proposal_no ? reduction_proposal_no : "",
          message_type: messageType,
        });
        return this.$router.push({
          name: "notification_subsidy_assess_msg_detail",
        });
      }
      this.expendedId = expendedId;
    },

    /**This function will minimize larger content make it short msg */
    readLess() {
      this.expendedId = "";
    },
    isLargeText(text) {
      return text.length > 80;
    },

    /**This function will slice messages greater than length 80 and add three dots(...) with that messages*/
    sliceText(text) {
      if (!this.isMobileScreen) {
        if (text.length >= 80) {
          return text.slice(0, 55) + "・・・";
        }
        return text;
      } else {
        if (text.length >= 40) {
          return text.slice(0, 30) + "・・・";
        }
        return text;
      }
    },
  },
};
</script>

<style scoped lang="scss">
$primary: #062854 !default;
$unread_color: #afabab;
$icon_background: red;
$shadow-color: #dbd6d6;

.icon-btn {
  height: 31px;
  width: 31px;
  min-width: 30px;
  border-radius: 50%;
  border: none;
  background-color: $icon_background;
  overflow: hidden;
  font-size: 0.555rem;
  border: 1px solid gray;
  padding: revert;
  cursor: default !important;
  background-image: none !important;
}

.icon-btn:hover {
  background-color: $icon_background;
}

.link {
  color: $primary;
  font-weight: 900;
}

.read-btn {
  min-width: 40px;
  padding: 2px 5px 2px 5px;
  font-size: 0.675rem;
  cursor: default !important;
}

.unread-btn:hover {
  background-color: $unread_color;
  border: 1px solid $unread_color;
}

.unread-btn {
  background-color: $unread_color !important;
  border: 1px solid $unread_color !important;
  min-width: 40px;
  padding: 2px 5px 2px 5px;
  font-size: 0.675rem;
  background-image: none !important;
  cursor: default;
  cursor: default !important;
}
hr.dashed {
  border-top: 1px dashed $primary;
  margin-top: 0rem;
}
.pagination {
  float: right;
}
.pd-23 {
  padding-left: 2.3rem;
}
.pd-28 {
  padding-left: 2.8rem;
}
.dropdownToggleBody {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #0000000f;
  border-radius: 4px;
  opacity: 1;
}
.reduction-logo {
  margin-top: 12px !important;
  margin-left: 0.675rem !important;
}
.subsidy-logo {
  margin-top: 6px !important;
  margin-left: 0.37rem !important;
}
.notification-logo {
  margin-top: 7px !important;
  margin-left: 0.5rem !important;
}
.thumbnailLogo {
  height: 40px;
  width: 40px;
  min-width: 40px;
  border-radius: 50%;
  border: none;
  border: none !important;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 4px 10px #00000021;
  opacity: 1;
}

.mntbtn {
  box-shadow: 0px 0px 40px #0000000f !important;
  background: transparent
    linear-gradient(194deg, #0e4a98 0%, var(--unnamed-color-003170) 100%) 0% 0%
    no-repeat padding-box;
  background: transparent linear-gradient(194deg, #0e4a98 0%, #003170 100%) 0%
    0% no-repeat padding-box;
  letter-spacing: 0px;
  color: #ffffff;
}
.mnt2btn {
  background-image: none !important;
  color: gray !important;
  background-color: white !important;
  box-shadow: 0px 0px 40px #0000000f !important;
  opacity: 1;
}
.border-radius-imprv {
  border-top-left-radius: 0px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 0px;
}
.mnt2border {
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 0px;
}
.mntborder {
  border-top-left-radius: 4px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 4px;
}
.page-link {
  border-radius: 0.2rem !important;
  border-color: #ffffff !important;
  box-shadow: 0 0 10px $shadow-color !important;
  -webkit-box-shadow: 0 0 10px $shadow-color !important;
  -moz-box-shadow: 0 0 10px $shadow-color !important;
  cursor: pointer;
  padding-top: 0.25rem;
  padding-right: 0.75rem;
  padding-bottom: 0.25rem;
  padding-left: 0.75rem;
}
button[role="menuitem"].page-link {
  border-radius: 50rem !important;
}
span[role="menuitem"].page-link {
  border-radius: 50rem !important;
}
.textClass {
  letter-spacing: 0px;
  color: #4b4b4b;
  opacity: 1;
  font-weight: 600 !important;
}
.messageClass {
  word-break: break-word !important;
}
.mobileAdjust {
  margin-left: -10px;
}
@media (max-width: 992px) {
  .page-item {
    font-size: 0.65rem !important;
  }

  .page-link {
    border-radius: 0.2rem !important;
    border-color: #ffffff !important;
    box-shadow: 0 0 10px $shadow-color !important;
    -webkit-box-shadow: 0 0 10px $shadow-color !important;
    -moz-box-shadow: 0 0 10px $shadow-color !important;
    padding-top: 0.01rem;
    padding-right: 0.15rem;
    padding-bottom: 0.01rem;
    padding-left: 0.15rem;
  }
  button[role="menuitem"].page-link {
    border-radius: 50rem !important;
  }
  span[role="menuitem"].page-link {
    border-radius: 50rem !important;
  }
  .textClass {
    letter-spacing: 0px;
    color: #4b4b4b;
    opacity: 1;
    font-weight: 600 !important;
  }
  .pd-23 {
    padding-left: 2.8rem;
  }
  .pd-28 {
    padding-left: 2.8rem;
  }
  .mobileMargin {
    margin-top: -2rem !important;
    margin-left: 1rem !important;
  }
  .padingAllign {
    padding-left: 12px !important;
    padding-right: 5px !important;
  }
}
</style>
